* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  /* color: black; */
}
html {
  background: linear-gradient(90deg, #FC5185 50%, #3FC1C9 50%);
}

.Container {
  background: linear-gradient(90deg, #FC5185 50%, #3FC1C9 50%);
  backdrop-filter: blur(0.5px);
  display: flex;
  flex-direction: column;
}



